/* global document, Office, Word */

Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    
    // Registrar el evento de cambio de documento
    Office.context.document.addHandlerAsync(
      "documentSelectionChanged", 
      () => {
        if (changeTimeout) {
          clearTimeout(changeTimeout);
        }
        changeTimeout = setTimeout(processDocumentChanges, 2000);
      }
    );
  }
});

let isProcessing = false;
let changeTimeout = null;

async function processDocumentChanges() {
  if (isProcessing) return;
  
  isProcessing = true;
  try {
    await Promise.all([
      tryCatch(replaceNumerals),
      tryCatch(formatSentencia),
      tryCatch(formatLatinExpressions),
      tryCatch(formatNumbers)
    ]);
  } finally {
    isProcessing = false;
  }
}

