async function formatSentencia() {
  await Word.run(async (context) => {
    const paragraphs = context.document.body.paragraphs;
    context.load(paragraphs, 'text');
    await context.sync();

    const replacements = {
      "sentencia": "SENTENCIA",
      "antecedentes": "ANTECEDENTES DE HECHO",
      "fundamentos": "FUNDAMENTOS DE DERECHO",
      "fallo": "FALLO",
      "parte": "PARTE DISPOSITIVA",
      "razonamientos": "RAZONAMIENTOS JURÍDICOS",
      "auto": "AUTO"
    };

    let modified = false;
    for (const paragraph of paragraphs.items) {
      context.load(paragraph, 'text');
      await context.sync();
      
      const trimmedText = paragraph.text.trim().toLowerCase();
      
      if (replacements.hasOwnProperty(trimmedText)) {
        paragraph.clear();
        await context.sync();
        const range = paragraph.insertText(replacements[trimmedText], Word.InsertLocation.start);
        range.font.bold = true;
        paragraph.alignment = Word.Alignment.centered;
        modified = true;
        await context.sync();
      }
    }
  });
}


