async function formatLatinExpressions() {
  await Word.run(async (context) => {
    try {
      const body = context.document.body;
      
      // Cargar todas las expresiones latinas
      for (const expression of latinExpressions) {
        // Buscar la expresión con y sin comillas
        const withQuotes = `"${expression}"`;
        const withoutQuotes = expression;
        
        // Buscar ambas variantes
        const searchResults = body.search(withoutQuotes, {
          matchCase: false,
          matchWholeWord: true,
          matchWildcards: false
        });
        
        const searchResultsQuoted = body.search(withQuotes, {
          matchCase: false,
          matchWholeWord: true,
          matchWildcards: false
        });
        
        context.load(searchResults, 'text');
        context.load(searchResultsQuoted, 'text');
        await context.sync();
        
        // Aplicar formato a resultados sin comillas
        if (searchResults.items && searchResults.items.length > 0) {
          for (let item of searchResults.items) {
            item.font.italic = true;
          }
        }
        
        // Aplicar formato a resultados con comillas
        if (searchResultsQuoted.items && searchResultsQuoted.items.length > 0) {
          for (let item of searchResultsQuoted.items) {
            // Mantener las comillas y aplicar cursiva a todo el texto
            item.font.italic = true;
          }
        }
        
        await context.sync();
      }
    } catch (error) {
      console.error('Error en formatLatinExpressions:', error);
    }
  });
}


async function formatNumbers() {
  await Word.run(async (context) => {
    try {
      const body = context.document.body;
      
      // Procesar números del 1 al 100
      for (let i = 1; i <= 100; i++) {
        const searchPattern = `${i}.-`;
        const searchResults = body.search(searchPattern, {
          matchCase: false,
          matchWholeWord: false
        });
        
        context.load(searchResults, 'text');
        await context.sync();
        
        if (searchResults.items && searchResults.items.length > 0) {
          for (let item of searchResults.items) {
            // Verificar si ya está en negrita para evitar procesamiento repetido
            context.load(item, 'font');
            await context.sync();
            
            if (!item.font.bold) {
              // Aplicar negrita solo al número y .-
              item.font.bold = true;
              
              // Verificar si ya hay un espacio después
              const afterRange = item.getRange(Word.RangeLocation.after);
              context.load(afterRange, 'text');
              await context.sync();
              
              // Solo añadir espacio si no hay uno y si no estamos al final del documento
              if (afterRange.text !== '' && !afterRange.text.startsWith(' ')) {
                item.insertText(" ", Word.InsertLocation.after);
              }
              
              await context.sync();
            }
          }
        }
      }
    } catch (error) {
      console.error('Error en formatNumbers:', error);
    }
  });
}


async function tryCatch(callback) {
  try {
    await callback();
  } catch (error) {
    console.error(error);
  }
}